import { PathRouteProps, Route, Routes, RoutesProps } from "react-router-dom";
import Home from "../containers/Home";
import GridSystem from "../containers/GridSystem";
import Chat from "../containers/Chat";
import Room from "../containers/Room";
import NotFound from "../containers/NotFound";
import Realtime from "../containers/Realtime";

const route: PathRouteProps[] = [
  {
    path: "/",
    element: <Home />,
  },
  {
    path: "/realtime",
    element: <Realtime />,
  },
  {
    path: "/chat",
    element: <Chat />,
  },
  {
    path: "/room",
    element: <Room />,
  },
  {
    path: "/test",
    element: <GridSystem />,
  },
  {
    path: "*",
    element: <NotFound />,
  },
];

export default function CustomerRoute({ location }: RoutesProps) {
  return (
    <>
      {/* {useRoutes(route)} */}
      <Routes location={location}>
        {route.map((item) => (
          <Route key={item.path} path={item.path} element={item.element} />
        ))}
      </Routes>
    </>
  );
}
