import { NavLink } from "react-router-dom";
import { NavItem } from "reactstrap";

export interface NavContent {
  title: string;
  to: string;
}

export const customerNavbar: NavContent[] = [
  {
    title: "Counter Redux",
    to: "/",
  },
  {
    title: "Realtime Test",
    to: "/realtime",
  },
  {
    title: "Chat",
    to: "/chat",
  },
  {
    title: "Room",
    to: "/room",
  },
  {
    title: "Test",
    to: "/test",
  },
];

export default function CustomNavbar() {
  return (
    <>
      {customerNavbar.map((item) => (
        <NavItem key={item.to}>
          <NavLink className="nav-link" to={item.to}>
            {item.title}
          </NavLink>
        </NavItem>
      ))}
    </>
  );
}
